
import { useState, useEffect } from 'react'
import { Vendor, VendorFilters as VendorFiltersType } from '@/types/purchase'
import { vendorService } from '@/services/vendorService'
import VendorFilters from '@/components/vendor/VendorFilters'
import VendorTable from '@/components/vendor/VendorTable'
import VendorModal from '@/components/vendor/VendorModal'
import { toast } from 'sonner'
// XLSX    import ( )

type ModalMode = 'create' | 'edit' | 'view'

export default function VendorMain() {
  const [vendors, setVendors] = useState<Vendor[]>([])
  const [filteredVendors, setFilteredVendors] = useState<Vendor[]>([])
  const [loading, setLoading] = useState(true)
  const [filters, setFilters] = useState<VendorFiltersType>({})
  
  //  
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [selectedVendor, setSelectedVendor] = useState<Vendor | null>(null)
  const [modalMode, setModalMode] = useState<ModalMode>('create')

  //   
  const loadVendors = async () => {
    setLoading(true)
    try {
      const result = await vendorService.getVendors(filters)
      
      if (result.success && result.data) {
        setVendors(result.data)
        setFilteredVendors(result.data)
      } else {
        toast.error(result.error || '   .')
      }
    } catch (error) {
      toast.error('     .')
    } finally {
      setLoading(false)
    }
  }

  //       (  )
  useEffect(() => {
    loadVendors()
  }, [filters])

  //  
  const handleCreateNew = () => {
    setSelectedVendor(null)
    setModalMode('create')
    setIsModalOpen(true)
  }

  const handleEdit = (vendor: Vendor) => {
    setSelectedVendor(vendor)
    setModalMode('edit')
    setIsModalOpen(true)
  }

  const handleView = (vendor: Vendor) => {
    setSelectedVendor(vendor)
    setModalMode('view')
    setIsModalOpen(true)
  }

  const handleModalClose = () => {
    setIsModalOpen(false)
    setSelectedVendor(null)
  }

  const handleSave = () => {
    loadVendors()
  }

  // Excel  ( import  )
  const handleExport = async () => {
    try {
      const result = await vendorService.getVendorsForExport()
      
      if (result.success && result.data) {
        // XLSX    import
        const XLSX = await import('xlsx')
        
        const ws = XLSX.utils.json_to_sheet(result.data)
        const wb = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(wb, ws, ' ')
        
        //    
        const today = new Date().toISOString().slice(0, 10)
        const filename = `__${today}.xlsx`
        
        XLSX.writeFile(wb, filename)
        toast.success('Excel  .')
      } else {
        toast.error(result.error || 'Excel  .')
      }
    } catch (error) {
      toast.error('Excel    .')
    }
  }

  if (loading && vendors.length === 0) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto"></div>
          <p className="mt-2 text-gray-600">   ...</p>
        </div>
      </div>
    )
  }

  return (
    <>
      <div className="space-y-6">
      {/*   */}
      <VendorFilters
        filters={filters}
        onFiltersChange={setFilters}
        onExport={handleExport}
        onCreateNew={handleCreateNew}
      />

      {/*   */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <div className="bg-white p-4 rounded-lg border">
          <div className="text-2xl font-bold text-hansl-600">
            {filteredVendors.length}
          </div>
          <div className="text-sm text-gray-600">  </div>
        </div>
        <div className="bg-white p-4 rounded-lg border">
          <div className="text-2xl font-bold text-green-600">
            {filteredVendors.filter(v => v.is_active).length}
          </div>
          <div className="text-sm text-gray-600"> </div>
        </div>
        <div className="bg-white p-4 rounded-lg border">
          <div className="text-2xl font-bold text-red-600">
            {filteredVendors.filter(v => !v.is_active).length}
          </div>
          <div className="text-sm text-gray-600"> </div>
        </div>
        <div className="bg-white p-4 rounded-lg border">
          <div className="text-2xl font-bold text-purple-600">
            {filteredVendors.filter(v => v.business_number).length}
          </div>
          <div className="text-sm text-gray-600"> </div>
        </div>
      </div>

      {/*   */}
      <div className="bg-white rounded-lg border">
        <div className="p-4 border-b">
          <div className="flex justify-between items-center">
            <h3 className="text-lg font-medium"> </h3>
            <div className="text-sm text-gray-600">
              {loading ? ' ...' : ` ${filteredVendors.length} `}
            </div>
          </div>
        </div>
        
        <VendorTable
          vendors={filteredVendors}
          onEdit={handleEdit}
          onView={handleView}
          onRefresh={loadVendors}
        />
      </div>

      {/*  */}
      <VendorModal
        isOpen={isModalOpen}
        onClose={handleModalClose}
        vendor={selectedVendor}
        onSave={handleSave}
        mode={modalMode}
      />
      </div>
    </>
  )
}