
import { useState } from 'react'
import { Vendor } from '@/types/purchase'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { 
  Table, 
  TableBody, 
  TableCell, 
  TableHead, 
  TableHeader, 
  TableRow 
} from '@/components/ui/table'
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu'
import { 
  MoreHorizontal, 
  Edit, 
  Trash2, 
  Eye, 
  ToggleLeft, 
  ToggleRight 
} from 'lucide-react'
import { vendorService } from '@/services/vendorService'
import { toast } from 'sonner'
import { useTableSort } from '@/hooks/useTableSort'
import { SortableHeader } from '@/components/ui/sortable-header'
import { MobileCard, MobileCardItem, MobileCardHeader, MobileCardActions } from '@/components/ui/mobile-card'

interface VendorTableProps {
  vendors: Vendor[]
  onEdit: (vendor: Vendor) => void
  onView: (vendor: Vendor) => void
  onRefresh: () => void
}

export default function VendorTable({ vendors, onEdit, onView, onRefresh }: VendorTableProps) {
  const [loadingId, setLoadingId] = useState<number | null>(null)
  const { sortedData, sortConfig, handleSort } = useTableSort(vendors, 'vendor_name', 'asc')

  const handleToggleStatus = async (vendor: Vendor) => {
    setLoadingId(vendor.id)
    try {
      const result = await vendorService.toggleVendorStatus(vendor.id)
      
      if (result.success) {
        toast.success(` ${vendor.is_active ? '' : ''}.`)
        onRefresh()
      } else {
        toast.error(result.error || '  .')
      }
    } catch (error) {
      toast.error('    .')
    } finally {
      setLoadingId(null)
    }
  }

  const handleDelete = async (vendor: Vendor) => {
    if (!confirm(` '${vendor.vendor_name}'  ?`)) {
      return
    }

    setLoadingId(vendor.id)
    try {
      const result = await vendorService.deleteVendor(vendor.id)
      
      if (result.success) {
        toast.success(' .')
        onRefresh()
      } else {
        toast.error(result.error || ' .')
      }
    } catch (error) {
      toast.error('   .')
    } finally {
      setLoadingId(null)
    }
  }

  const formatDate = (dateString?: string) => {
    if (!dateString) return '-'
    return new Date(dateString).toLocaleDateString('ko-KR')
  }

  return (
    <>
      {/* Desktop Table View */}
      <div className="hidden md:block border rounded-lg overflow-hidden">
        <div className="overflow-x-auto">
          <Table>
        <TableHeader>
          <TableRow>
            <TableHead>
              <SortableHeader
                sortKey="vendor_name"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('vendor_name' as keyof Vendor)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead> </TableHead>
            <TableHead> </TableHead>
            <TableHead>
              <SortableHeader
                sortKey="created_at"
                currentSortKey={sortConfig.key as string | null}
                sortDirection={sortConfig.direction}
                onSort={() => handleSort('created_at' as keyof Vendor)}
              >
                
              </SortableHeader>
            </TableHead>
            <TableHead className="w-[100px]"></TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {vendors.length === 0 ? (
            <TableRow>
              <TableCell colSpan={5} className="text-center py-8 text-gray-500">
                  .
              </TableCell>
            </TableRow>
          ) : (
            sortedData.map((vendor) => (
              <TableRow key={vendor.id}>
                <TableCell className="font-medium">
                  {vendor.vendor_name}
                </TableCell>
                <TableCell>
                  <Badge variant="outline">
                    {vendor.vendor_contacts?.length || 0}
                  </Badge>
                </TableCell>
                <TableCell>
                  <div className="space-y-1">
                    {vendor.vendor_contacts && vendor.vendor_contacts.length > 0 ? (
                      vendor.vendor_contacts.slice(0, 2).map((contact: any, idx: number) => (
                        <div key={idx} className="text-sm">
                          <span className="font-medium">{contact.contact_name}</span>
                          <span className="text-gray-500 ml-2">{contact.contact_phone}</span>
                          {contact.contact_email && (
                            <span className="text-gray-500 ml-2">{contact.contact_email}</span>
                          )}
                        </div>
                      ))
                    ) : (
                      <span className="text-gray-400"> </span>
                    )}
                    {vendor.vendor_contacts && vendor.vendor_contacts.length > 2 && (
                      <span className="text-sm text-gray-500"> {vendor.vendor_contacts.length - 2}</span>
                    )}
                  </div>
                </TableCell>
                <TableCell>{formatDate(vendor.created_at)}</TableCell>
                <TableCell>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button
                        variant="ghost"
                        className="h-8 w-8 p-0"
                        disabled={loadingId === vendor.id}
                      >
                        <MoreHorizontal className="h-4 w-4" />
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent align="end">
                      <DropdownMenuItem onClick={() => onView(vendor)}>
                        <Eye className="mr-2 h-4 w-4" />
                         
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => onEdit(vendor)}>
                        <Edit className="mr-2 h-4 w-4" />
                        
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => handleToggleStatus(vendor)}>
                        {vendor.is_active ? (
                          <>
                            <ToggleLeft className="mr-2 h-4 w-4" />
                            
                          </>
                        ) : (
                          <>
                            <ToggleRight className="mr-2 h-4 w-4" />
                            
                          </>
                        )}
                      </DropdownMenuItem>
                      <DropdownMenuItem 
                        onClick={() => handleDelete(vendor)}
                        className="text-red-600"
                      >
                        <Trash2 className="mr-2 h-4 w-4" />
                        
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </TableCell>
              </TableRow>
            ))
          )}
        </TableBody>
          </Table>
        </div>
      </div>

      {/* Mobile Card View */}
      <div className="block md:hidden space-y-3">
        {sortedData.length === 0 ? (
          <div className="text-center py-8 text-gray-500">
              .
          </div>
        ) : (
          sortedData.map((vendor) => (
            <MobileCard key={vendor.id}>
              <MobileCardHeader>
                <div className="flex justify-between items-center">
                  <span>{vendor.vendor_name}</span>
                  <Badge variant="outline">
                    {vendor.vendor_contacts?.length || 0}
                  </Badge>
                </div>
              </MobileCardHeader>
              
              <MobileCardItem 
                label="" 
                value={
                  <div className="space-y-1">
                    {vendor.vendor_contacts && vendor.vendor_contacts.length > 0 ? (
                      vendor.vendor_contacts.slice(0, 2).map((contact: any, idx: number) => (
                        <div key={idx} className="text-sm">
                          <div className="font-medium">{contact.contact_name}</div>
                          <div className="text-gray-500 text-xs">{contact.contact_phone}</div>
                          {contact.contact_email && (
                            <div className="text-gray-500 text-xs">{contact.contact_email}</div>
                          )}
                        </div>
                      ))
                    ) : (
                      <span className="text-gray-400"> </span>
                    )}
                    {vendor.vendor_contacts && vendor.vendor_contacts.length > 2 && (
                      <span className="text-sm text-gray-500"> {vendor.vendor_contacts.length - 2}</span>
                    )}
                  </div>
                } 
              />
              <MobileCardItem label="" value={formatDate(vendor.created_at)} />
              
              <MobileCardActions>
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => onView(vendor)}
                >
                  <Eye className="w-4 h-4" />
                </Button>
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => onEdit(vendor)}
                >
                  <Edit className="w-4 h-4" />
                </Button>
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => handleToggleStatus(vendor)}
                  disabled={loadingId === vendor.id}
                >
                  {vendor.is_active ? (
                    <ToggleLeft className="w-4 h-4" />
                  ) : (
                    <ToggleRight className="w-4 h-4" />
                  )}
                </Button>
                <Button
                  size="sm"
                  variant="outline"
                  className="text-red-600"
                  onClick={() => handleDelete(vendor)}
                  disabled={loadingId === vendor.id}
                >
                  <Trash2 className="w-4 h-4" />
                </Button>
              </MobileCardActions>
            </MobileCard>
          ))
        )}
      </div>
    </>
  )
}