
import { useState } from 'react'
import { VendorFilters as VendorFiltersType } from '@/types/purchase'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select'
import { Search, X, Download, Plus } from 'lucide-react'

interface VendorFiltersProps {
  filters: VendorFiltersType
  onFiltersChange: (filters: VendorFiltersType) => void
  onExport: () => void
  onCreateNew: () => void
}

export default function VendorFilters({ 
  filters, 
  onFiltersChange, 
  onExport,
  onCreateNew 
}: VendorFiltersProps) {
  const [localSearch, setLocalSearch] = useState(filters.search || '')

  const handleSearchSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onFiltersChange({
      ...filters,
      search: localSearch.trim() || undefined
    })
  }

  const handleStatusChange = (value: string) => {
    onFiltersChange({
      ...filters,
      is_active: value === 'all' ? undefined : value === 'active'
    })
  }

  const clearFilters = () => {
    setLocalSearch('')
    onFiltersChange({})
  }

  const hasFilters = filters.search || filters.is_active !== undefined

  return (
    <div className="space-y-4">
      {/*    */}
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold text-gray-900"> </h2>
        <div className="flex gap-2">
          <Button
            variant="outline"
            onClick={onExport}
            className="flex items-center gap-2"
          >
            <Download className="w-4 h-4" />
            Excel 
          </Button>
          <Button 
            onClick={onCreateNew}
            className="flex items-center gap-2"
          >
            <Plus className="w-4 h-4" />
             
          </Button>
        </div>
      </div>

      {/*   */}
      <div className="bg-white p-4 rounded-lg border space-y-4">
        <form onSubmit={handleSearchSubmit} className="flex gap-4 items-end">
          {/*  */}
          <div className="flex-1">
            <label className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
              <Input
                type="text"
                placeholder=", , , ,  "
                value={localSearch}
                onChange={(e) => setLocalSearch(e.target.value)}
                className="pl-10"
              />
            </div>
          </div>

          {/*   */}
          <div className="sm:min-w-[120px]">
            <label className="block text-sm font-medium text-gray-700 mb-1">
              
            </label>
            <Select
              value={
                filters.is_active === undefined 
                  ? 'all' 
                  : filters.is_active 
                    ? 'active' 
                    : 'inactive'
              }
              onValueChange={handleStatusChange}
            >
              <SelectTrigger>
                <SelectValue />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all"></SelectItem>
                <SelectItem value="active"></SelectItem>
                <SelectItem value="inactive"></SelectItem>
              </SelectContent>
            </Select>
          </div>

          {/*     */}
          <div className="flex gap-2">
            <Button type="submit">
              
            </Button>
            {hasFilters && (
              <Button 
                type="button" 
                variant="outline" 
                onClick={clearFilters}
                className="flex items-center gap-1"
              >
                <X className="w-4 h-4" />
                
              </Button>
            )}
          </div>
        </form>
      </div>
    </div>
  )
}